import React, { useState, useEffect } from 'react';
import { 
  TextField, 
  Button, 
  Box, 
  Paper, 
  Typography, 
  List, 
  ListItem, 
  ListItemText, 
  ListItemSecondaryAction,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Checkbox,
  Link,
  InputAdornment
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import AddIcon from '@mui/icons-material/Add';
import LinkIcon from '@mui/icons-material/Link';
import CryptoJS from 'crypto-js';

const STORAGE_KEY = 'encrypted_notes';
const PASSWORD_MARKER = '###PASSWORD_MARKER###';
const SYSTEM_NOTE_ID_PREFIX = '2024'; //     ID
const FAKE_LIST_KEY = 'show_fake_list'; //      
const SALT = 'encrypted_notepad_salt'; //     

//   YouTube 
const FAKE_YOUTUBE_LIST = [
  { id: 1, text: 'https://www.youtube.com/watch?v=dQw4w9WgXcQ', completed: false },
  { id: 2, text: 'https://www.youtube.com/watch?v=9bZkp7q19f0', completed: true },
  { id: 3, text: 'https://www.youtube.com/watch?v=OPf0YbXqDm0', completed: false },
  { id: 4, text: 'https://www.youtube.com/watch?v=JGwWNGJdvx8', completed: true },
];

//     
const generateNoteKey = () => {
  return CryptoJS.lib.WordArray.random(256/8).toString();
};

//       
const createKeyFromPassword = (password) => {
  return CryptoJS.PBKDF2(password, SALT, {
    keySize: 256/32,
    iterations: 10000,
    hasher: CryptoJS.algo.SHA256
  });
};

const Notepad = () => {
  const [notes, setNotes] = useState(() => {
    //    localStorage   
    const savedNotes = localStorage.getItem(STORAGE_KEY);
    if (savedNotes) {
      try {
        return JSON.parse(savedNotes);
      } catch (error) {
        console.error('   :', error);
        return [];
      }
    }
    return [];
  });
  
  const [currentNote, setCurrentNote] = useState(null);
  const [text, setText] = useState('');
  const [password, setPassword] = useState('');
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [isNewNote, setIsNewNote] = useState(true);
  const [showPasswordField, setShowPasswordField] = useState(() => {
    //     
    return !localStorage.getItem(FAKE_LIST_KEY);
  });
  const [fakeList, setFakeList] = useState(FAKE_YOUTUBE_LIST);
  const [newLink, setNewLink] = useState('');
  const [isAddLinkDialogOpen, setIsAddLinkDialogOpen] = useState(false);
  const [showFakeList, setShowFakeList] = useState(() => {
    //    
    return localStorage.getItem(FAKE_LIST_KEY) === 'true';
  });

  //      
  useEffect(() => {
    if (showFakeList) {
      localStorage.setItem(FAKE_LIST_KEY, 'true');
    } else {
      localStorage.removeItem(FAKE_LIST_KEY);
    }
  }, [showFakeList]);

  //    
  useEffect(() => {
    try {
      localStorage.setItem(STORAGE_KEY, JSON.stringify(notes));
    } catch (error) {
      console.error('   :', error);
    }
  }, [notes]);

  const checkPassword = (password, notes) => {
    const key = createKeyFromPassword(password);

    for (const note of notes) {
      try {
        const decryptedContent = CryptoJS.AES.decrypt(note.content, key.toString()).toString(CryptoJS.enc.Utf8);
        if (decryptedContent.startsWith(PASSWORD_MARKER)) {
          const savedHash = decryptedContent.slice(PASSWORD_MARKER.length);
          const currentHash = key.toString();
          
          if (savedHash === currentHash) {
            return true;
          }
        }
      } catch (error) {
        continue;
      }
    }
    return false;
  };

  const handleAuthenticate = () => {
    if (!password) {
      alert(',  ');
      return;
    }

    if (notes.length === 0) {
      const key = createKeyFromPassword(password);
      const markerContent = `${PASSWORD_MARKER}${key.toString()}`;
      const encryptedContent = CryptoJS.AES.encrypt(markerContent, key.toString()).toString();
      
      const systemNote = {
        id: String(`${SYSTEM_NOTE_ID_PREFIX}${Date.now()}`),
        content: encryptedContent,
        createdAt: new Date().toISOString(),
        updatedAt: new Date().toISOString()
      };

      setNotes([systemNote]);
      setIsAuthenticated(true);
    } else {
      const isValidPassword = checkPassword(password, notes);

      if (isValidPassword) {
        setIsAuthenticated(true);
        setShowFakeList(false);
        localStorage.removeItem(FAKE_LIST_KEY);
      } else {
        setShowFakeList(true);
        setShowPasswordField(false);
        localStorage.setItem(FAKE_LIST_KEY, 'true');
      }
    }
  };

  const handleFakeItemClick = (item) => {
    if (item.text.includes('anothertry')) {
      setShowPasswordField(true);
      setPassword('');
    }
    setFakeList(prevList => 
      prevList.map(listItem => 
        listItem.id === item.id 
          ? { ...listItem, completed: !listItem.completed }
          : listItem
      )
    );
  };

  const handleSaveNote = () => {
    if (!isAuthenticated) {
      alert(',   ');
      return;
    }

    if (!password) {
      alert(',  ');
      return;
    }

    try {
      const key = createKeyFromPassword(password);
      const noteKey = generateNoteKey();
      
      //     
      const encryptedText = CryptoJS.AES.encrypt(text, noteKey).toString();
      
      //     
      const encryptedNoteKey = CryptoJS.AES.encrypt(noteKey, key.toString()).toString();

      const newNote = {
        id: String(isNewNote ? Date.now() : currentNote.id),
        content: encryptedText,
        key: encryptedNoteKey, //    
        createdAt: isNewNote ? new Date().toISOString() : currentNote.createdAt,
        updatedAt: new Date().toISOString()
      };

      if (isNewNote) {
        setNotes(prevNotes => [...prevNotes, newNote]);
      } else {
        setNotes(prevNotes => prevNotes.map(note => note.id === currentNote.id ? newNote : note));
      }

      handleCloseDialog();
    } catch (error) {
      alert('   ');
      setIsAuthenticated(false);
      setPassword('');
    }
  };

  const handleOpenDialog = (note = null) => {
    if (!isAuthenticated) {
      alert(',   ');
      return;
    }

    if (note) {
      try {
        const key = createKeyFromPassword(password);
        //    
        const noteKey = CryptoJS.AES.decrypt(note.key, key.toString()).toString(CryptoJS.enc.Utf8);
        //    
        const decryptedContent = CryptoJS.AES.decrypt(note.content, noteKey).toString(CryptoJS.enc.Utf8);
        setCurrentNote(note);
        setText(decryptedContent);
        setIsNewNote(false);
      } catch (error) {
        alert(' ');
        setIsAuthenticated(false);
        setPassword('');
      }
    } else {
      setCurrentNote(null);
      setText('');
      setIsNewNote(true);
    }
    setIsDialogOpen(true);
  };

  const handleCloseDialog = () => {
    setIsDialogOpen(false);
    setCurrentNote(null);
    setText('');
    setIsNewNote(true);
  };

  const handleDeleteNote = (noteId) => {
    if (!isAuthenticated) {
      alert(',   ');
      return;
    }

    if (window.confirm(' ,     ?')) {
      setNotes(prevNotes => prevNotes.filter(note => note.id !== noteId));
    }
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleString('ru-RU');
  };

  const handleAddLink = () => {
    if (newLink === 'anothertry') {
      setShowPasswordField(true);
      setPassword('');
      setShowFakeList(false);
    } else {
      setFakeList(prevList => [...prevList, { 
        id: Date.now(), 
        text: newLink, 
        completed: false 
      }]);
    }
    setNewLink('');
    setIsAddLinkDialogOpen(false);
  };

  const getNoteTitle = (note) => {
    if (!isAuthenticated) {
      return '';
    }

    try {
      const key = createKeyFromPassword(password);
      const noteKey = CryptoJS.AES.decrypt(note.key, key.toString()).toString(CryptoJS.enc.Utf8);
      const decryptedContent = CryptoJS.AES.decrypt(note.content, noteKey).toString(CryptoJS.enc.Utf8);
      if (decryptedContent.startsWith(PASSWORD_MARKER)) {
        return '';
      }
      return decryptedContent.split('\n')[0].slice(0, 50) || '';
    } catch (error) {
      return '';
    }
  };

  if (!isAuthenticated) {
    return (
      <Box sx={{ maxWidth: 600, mx: 'auto', mt: 4 }}>
        <Typography variant="h4" sx={{ mb: 3 }}>
          {showFakeList ? '   ' : '  '}
        </Typography>
        
        {showPasswordField && (
          <Paper sx={{ p: 2, mb: 3 }}>
            <form onSubmit={(e) => {
              e.preventDefault();
              handleAuthenticate();
            }}>
              <TextField
                fullWidth
                label=""
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                sx={{ mb: 2 }}
              />
              <Button 
                variant="contained" 
                onClick={handleAuthenticate}
                fullWidth
                type="submit"
              >
                
              </Button>
            </form>
          </Paper>
        )}

        {showFakeList && (
          <>
            <Box sx={{ mb: 2 }}>
              <Button
                variant="outlined"
                startIcon={<AddIcon />}
                onClick={() => setIsAddLinkDialogOpen(true)}
                fullWidth
              >
                 
              </Button>
            </Box>

            <List>
              {fakeList.map((item) => (
                <Paper 
                  key={item.id} 
                  sx={{ 
                    mb: 1,
                    cursor: 'pointer',
                    '&:hover': {
                      backgroundColor: 'action.hover'
                    }
                  }}
                  onClick={() => handleFakeItemClick(item)}
                >
                  <ListItem>
                    <Checkbox 
                      checked={item.completed}
                      disabled
                      sx={{ mr: 2 }}
                    />
                    <ListItemText
                      primary={
                        <Link 
                          href={item.text} 
                          target="_blank" 
                          rel="noopener noreferrer"
                          sx={{
                            textDecoration: item.completed ? 'line-through' : 'none',
                            color: item.completed ? 'text.secondary' : 'primary.main'
                          }}
                        >
                          {item.text}
                        </Link>
                      }
                    />
                  </ListItem>
                </Paper>
              ))}
            </List>

            <Dialog 
              open={isAddLinkDialogOpen} 
              onClose={() => setIsAddLinkDialogOpen(false)}
              maxWidth="sm"
              fullWidth
            >
              <DialogTitle>
                 
              </DialogTitle>
              <DialogContent>
                <Box sx={{ mt: 2 }}>
                  <TextField
                    fullWidth
                    label="  "
                    value={newLink}
                    onChange={(e) => setNewLink(e.target.value)}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <LinkIcon />
                        </InputAdornment>
                      ),
                    }}
                  />
                </Box>
              </DialogContent>
              <DialogActions>
                <Button onClick={() => setIsAddLinkDialogOpen(false)}></Button>
                <Button onClick={handleAddLink} variant="contained">
                  
                </Button>
              </DialogActions>
            </Dialog>
          </>
        )}
      </Box>
    );
  }

  return (
    <Box sx={{ maxWidth: 1200, mx: 'auto', mt: 4 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4">
           
        </Typography>
        <Button
          variant="contained"
          startIcon={<AddIcon />}
          onClick={() => handleOpenDialog()}
        >
           
        </Button>
      </Box>

      <List>
        {notes.filter(note => !note.id.startsWith(SYSTEM_NOTE_ID_PREFIX)).map((note) => (
          <Paper key={note.id} sx={{ mb: 1 }}>
            <ListItem>
              <ListItemText
                primary={getNoteTitle(note)}
                secondary={`: ${formatDate(note.updatedAt)}`}
              />
              <ListItemSecondaryAction>
                <IconButton edge="end" onClick={() => handleOpenDialog(note)} sx={{ mr: 1 }}>
                  <EditIcon />
                </IconButton>
                <IconButton edge="end" onClick={() => handleDeleteNote(note.id)}>
                  <DeleteIcon />
                </IconButton>
              </ListItemSecondaryAction>
            </ListItem>
          </Paper>
        ))}
      </List>

      <Dialog 
        open={isDialogOpen} 
        onClose={handleCloseDialog}
        maxWidth="md"
        fullWidth
      >
        <DialogTitle>
          {isNewNote ? ' ' : ' '}
        </DialogTitle>
        <DialogContent>
          <Box sx={{ mt: 2 }}>
            <TextField
              fullWidth
              multiline
              rows={6}
              label=" "
              value={text}
              onChange={(e) => setText(e.target.value)}
              sx={{ mb: 2 }}
            />
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDialog}></Button>
          <Button onClick={handleSaveNote} variant="contained">
            
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default Notepad; 